import React from 'react'
import './central.css'
import disas1 from '../../images/disas1.jpg'
import disas2 from '../../images/disas2.jpg'
import disas3 from '../../images/disas3.jpg'
import disas4 from '../../images/disas4.jpg'
import disas5 from '../../images/disas5.jpg'
import disas6 from '../../images/disas6.JPG'
import Toggle from '../Toggle'

var popup = [ { original: "https://thitgorn.github.io/static/media/disas1.c5e94995.jpg" ,
                thumbnail: "https://thitgorn.github.io/static/media/disas1.c5e94995.jpg" } ,
              { original: "https://thitgorn.github.io/static/media/disas2.41ef1470.jpg" ,
                thumbnail: "https://thitgorn.github.io/static/media/disas2.41ef1470.jpg" } ,
              { original: "https://thitgorn.github.io/static/media/disas3.5d7e8ab8.jpg" ,
                thumbnail: "https://thitgorn.github.io/static/media/disas3.5d7e8ab8.jpg" } ,
              { original: "https://thitgorn.github.io/static/media/disas4.696e19e7.jpg" ,
                thumbnail: "https://thitgorn.github.io/static/media/disas4.696e19e7.jpg" } ,
              { original: "https://thitgorn.github.io/static/media/disas5.67460c07.jpg" ,
                thumbnail: "https://thitgorn.github.io/static/media/disas5.67460c07.jpg" } ,
              { original: "https://thitgorn.github.io/static/media/disas6.9830bc8c.JPG" ,
                thumbnail: "https://thitgorn.github.io/static/media/disas6.9830bc8c.JPG" } ]

class DisasterBox extends React.Component {
  constructor() {
    super()
    this.state = { click : false}
    this.toggle = this.toggle.bind(this)
  }

  toggle() {
    var prev = !this.state.click
    this.setState( {click : prev} )
    console.log(this.state.click);
  }

  render() {
    return (<div>
                <h3 onClick={this.toggle}>Disaster-box</h3>
                <Toggle toggle={this.toggle} click={this.state.click} element={popup}/>
            </div>);
  }
}

export default DisasterBox;



